/* App.css */

.App {
    text-align: center;
    background-color: #97f3fe; /* Light Blue background */
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #528589; /* Color for the text */
}

.App-header {
    background-color: #282c34;
    min-height: 50px;
    padding: 20px;
    color: white;
}

.Counter-display {
    font-size: 5em; /* Large bold number display */
    /* Add animations here if desired*/
}

.Fraction-display {
    display: inline-block; /* Adjust as needed */
    vertical-align: middle;
    font-size: 0.33em; /* A third of the size of your counter */
}

.Counter-controls button {
    font-size: 1em;
    margin: 0.5em;
    padding: 1em;
    background-color: #528589; /* Color for the buttons */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

    .Counter-controls button:hover {
        background-color: #6fa9ab; /* Lighter color on hover */
    }

/* Responsive design for smaller devices */
@media (max-width: 768px) {
    .Counter-display {
        font-size: 3em; /* Adjust the size for smaller screens */
    }

    /* Add any other specific adjustments for your control buttons here */
}
